import Vue from 'vue'
import VueRouter from 'vue-router'
import Campaigns from '../components/Campaigns'
import Login from '../components/Login'
import Register from '../components/Register'
import Nav from '../components/subassembly/Nav'
import XiangQing from '../components/subassembly/xiangqing'
import XiuGai from '../components/subassembly/xiugai'
import GuanLi from '../components/subassembly/guanli'
import HuoDong from "../components/subassembly/HuoDong";

Vue.use(VueRouter)

export default new VueRouter({
    routes:[
        {
            path:"/",
            name:"Login",
            component:Login,
            meta:{
                title:"登录页"
            }
        },{
            path:"/register",
            name:"Register",
            component:Register,
            meta:{
                title:"注册页"
            }
        },
        {
            path:"/main", component:Campaigns,children: [
                {
                    path:"/manage",component: HuoDong,
                    meta:{
                        title:"活动中心"
                    }
                },
                {
                    path:"/nav",component:Nav,
                    children:[
                        {
                            path:"",component:XiangQing,meta:{
                                title:"账户详情"
                            }
                        },
                        {
                            path:"guanli",component:GuanLi,meta:{
                                title:"子账户管理"
                            }
                        },
                        {
                            path:"xiugai",component:XiuGai,meta:{
                                title:"密码修改"
                            }
                        }
                    ]
                }
            ]
        }
        ]
})