<template>
    <div class="system">
        <h2>账户信息管理</h2>
        <div class="form">
            <div class="wid-1"><i>预警金额</i></div>
            <div class="wid-2">
                <select class="yangshi" v-model="value">
                    <option vlaue="0">0</option>
                    <option vlaue="20">20</option>
                    <option vlaue="100">100</option>
                    <option vlaue="500">500</option>
                    <option value="1000">1000</option>
                    <option value="2000">2000</option>
                </select>
            </div>
            <div class="wid-1"><i>手机号</i></div>
            <div class="wid-2">
                <input class="yangshi" v-model="tel"/>
            </div>
            <div class="wid-1"><i>邮箱</i></div>
            <div class="wid-2">
                <input  class="yangshi" v-model="eml"/>
            </div>
            <div class="wid-2">
                <input type="submit" class="button" @click="tijiao" value="提交" />
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "guanli",
        data:function () {
            return {
                value:localStorage.min,
                tel:localStorage.tel,
                eml:localStorage.elail
            }
        },
        methods:{
            tijiao:function () {
                let re = /^[A-Za-z\d]+([-_.][A-Za-z\d]+)*@([A-Za-z\d]+[-.])+[A-Za-z\d]{2,4}$/;
                if(this.value!==localStorage.min||this.tel!=localStorage.tel||this.eml!=localStorage.elail){
                    this.$confirm('检测到信息变更, 是否修改信息?', '提示', {
                        confirmButtonText: '确定',
                        cancelButtonText: '取消',
                        type: 'warning'
                    }).then(() => {
                        if(!(/^1[34578]\d{9}$/.test(this.tel))){
                            alert("请输入正确的手机号！");
                        }else if(!re.test(this.eml)){
                            alert("邮箱格式错误！");
                        }else{
                            localStorage.min=this.value;
                            localStorage.tel=this.tel;
                            localStorage.elail=this.eml;
                            this.$notify({
                                title: '恭喜',
                                message: '修改成功！',
                                type: 'success'
                            });
                        }
                    }).catch(() => {
                        this.$message({
                            type: 'info',
                            message: '已取消'
                        });
                    });
                }else{
                    this.$message('你没有修改任何信息!');
                }
            }
        }
    }
</script>

<style scoped>
    .system{border:1px solid #e8e8e8;}
    .system>h2{height: 46px;line-height: 46px;padding:0 10px;border-bottom:1px solid #e8e8e8;background: #f4f4f4;font-size: 15px;color: #555;}
    i{display:inline-block;color: #777;font-size: 14px;line-height: 1.5em;font-style:normal;}
    .form{width: 466px;height: 160px;margin:0 auto;padding-top: 20px;}
    .form>div{height: 40px;}
    .wid-1{width: 33.33333%;float: left;}
    .wid-2{width: 66.66666%;float: right;}
    .yangshi{width: 100%;height: 30px;border:1px #dcdfe6 solid;color: #777;box-sizing: border-box;padding-left: 10px;}
    .button{width: 150px;height: 30px;background-color: #0099cc;border:none;border-radius:0;color: #fff;}
</style>