import Vue from 'vue'
import App from './App.vue'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import router from './router'

Vue.use(ElementUI);
Vue.config.productionTip = false

router.beforeEach((to,from,next) =>{
  if(to.path==="/"||to.path==="/register"){
    next()
  }else{
    if(localStorage.static=="caihai"){
      next()
    }else{
      next({path:"/"})
    }
  }
  if(to.meta.title){
    document.title = to.meta.title;
  }
})

new Vue({
  render: h => h(App),
  router
}).$mount('#app')

window.onload=function(){
  console.log("同学，祝贺你喜提彩蛋~\n在这里你可以查看或修改一些网站中的数据\n" +
      "比如你注册之后产生的所有数据就存储在localStorage中~\n你可以在下面直接输入localStorage.name,然后点击回车，你会看见你的用户名，前提是你已经注册过了。\n" +
      "localStorage.password是你的密码，你可以在这里随意修改而不受任何限制！\n" +
      "localStorage.clear()可以清除localStorage的所有数据！\n%c再写一段红色的废话！！","color:red");
  console.table(localStorage);
};


