<template>
    <div class="xiugai">
        <h2>密码修改</h2>
        <div class="form">
            <div class="wid-8">{{tishi.a}}</div>
            <div class="wid-1"><i>原密码</i></div>
            <div class="wid-2">
                <input type="password" class="yangshi" maxlength="9" v-model="shuju.mima1"/>
            </div>
            <div class="wid-8">{{tishi.b}}</div>
            <div class="wid-1"><i>新密码</i></div>
            <div class="wid-2">
                <input type="password" class="yangshi" maxlength="9" v-model="shuju.mima2"/>
            </div>
            <div class="wid-8">{{tishi.c}}</div>
            <div class="wid-1"><i>确认新密码</i></div>
            <div class="wid-2">
                <input type="password" class="yangshi" maxlength="9" v-model="shuju.mima3"/>
            </div>
            <div class="wid-2">
                <input type="submit" @click="tijiao" class="button" value="提交" />
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "xiugai",
        data:function () {
            return {
                shuju:{
                    mima1:"",
                    mima2:"",
                    mima3:""
                },
                tishi:{
                    a:"",
                    b:"",
                    c:""
                }
            }
        },
        methods:{
            tijiao:function () {
                if(this.shuju.mima1==""||this.shuju.mima1==null){
                    this.tishi.a="请输入原密码";
                    this.tishi.b=this.tishi.c="";
                }else if(this.shuju.mima2==""||this.shuju.mima2==null){
                    this.tishi.c=this.tishi.a="";
                    this.tishi.b="请输入新密码"
                }else if(this.shuju.mima3==""||this.shuju.mima3==null){
                    this.tishi.a=this.tishi.b="";
                    this.tishi.c="请再次输入新密码";
                }else if(this.shuju.mima2!=this.shuju.mima3){
                    this.tishi.a=this.tishi.b="";
                    this.tishi.c="两次输入密码不一致";
                }else if(this.shuju.mima1!=localStorage.password){
                    this.tishi.a=this.tishi.b=this.tishi.c="";
                    this.$message.error('修改失败，原密码错误！');
                    this.shuju.mima1="";
                }else if(this.shuju.mima2==localStorage.password){
                    this.tishi.b="新密码不能与原密码一致";
                }else if(this.shuju.mima1==localStorage.password){
                    this.tishi.a=this.tishi.b=this.tishi.c="";
                    localStorage.password=this.shuju.mima2;
                    this.$notify({
                        title: '成功',
                        message: '修改成功',
                        type: 'success'
                    });
                    this.shuju.mima1=this.shuju.mima2=this.shuju.mima3="";
                }
            }
        }
    }
</script>

<style scoped>
    .xiugai{border:1px solid #e8e8e8;}
    .xiugai>h2{height: 46px;line-height: 46px;padding:0 10px;border-bottom:1px solid #e8e8e8;background: #f4f4f4;font-size: 15px;color: #555;}
    i{display:inline-block;color: #777;font-size: 14px;line-height: 1.5em;font-style:normal;}
    .form{width: 466px;height:210px;margin:0 auto;padding-top: 20px;}
    .wid-1{width: 33.33333%;height: 30px;float: left;}
    .wid-2{width: 66.66666%;height: 30px;float: right;}
    .wid-8{width: 100%;height: 20px;padding-left: 33.33333%;color: #eb6100;float: left;}
    .yangshi{width: 100%;height: 30px;border:1px #dcdfe6 solid;color: #777;box-sizing: border-box;padding-left: 10px;}
    .button{width: 150px;height: 30px;margin-top: 20px;background-color: #0099cc;border:none;border-radius:0;color: #fff;}
</style>