<template>
    <div class="login">
        <Head></Head>
        <div class="main">
            <div class="logo"></div>
            <div class="form">
                <h2>用户登录</h2>
                <div style="height: 20px;"><span class="alert">{{alert}}</span></div>
                <el-input size="small" v-model="name" placeholder="用户名" maxlength="12"></el-input>
                <el-input size="small" v-model="password" placeholder="密码" type="password" maxlength="9"></el-input>
                <el-button class="button" size="small" @click="enter">登录</el-button>
                <el-button size="small" @click="login">注册</el-button>
                <el-button class="rig" type="text" @click="find">忘记密码?</el-button>
            </div>
        </div>
        <Footer class="footer"></Footer>
    </div>
</template>

<script>
    import Footer from "./subassembly/Footer"
    import Head from "./subassembly/Head"
    export default {
        name: "login",
        components:{
            Footer,Head
        },
        data:function () {
            return {
                name:"",
                password:"",
                alert:""
            }
        },
        methods:{
            enter:function () {
                if(this.name==""||this.password==""){
                    this.alert="用户名或密码不能为空"
                }else if(this.name!=localStorage.name||this.password!=localStorage.password){
                    this.alert="用户名或密码错误"
                }else if(this.name==localStorage.name||this.password==localStorage.password){
                    this.alert="";
                    this.$router.push('/manage');
                }
            },
            find:function () {
                alert(localStorage.password);
            },
            login:function () {
                this.$confirm('本网站连AJAX都没有，注册产生的所有数据均保存在本地浏览器中，请放心玩耍！！', '温馨提示', {
                    distinguishCancelAndClose: true,
                    confirmButtonText: '立即注册',
                    cancelButtonText: '垃圾，不注册了'
                }).then(() => {
                    this.$router.push('/register');
                }).catch(() => {

                })
            }
        }
    }
</script>

<style scoped>
    .main{max-width: 1360px;min-width: 1170px;margin:70px auto;overflow: hidden;}
    .logo,.form{width: 50%;height: 500px;float: left;}
    .logo{background-image:url("../assets/login-img3.png");background-size:50%;background-repeat:no-repeat;background-position:50% 60%;}
    .form{padding-top:5%;padding-right:25%;box-sizing: border-box;}
    .form h2{font-size: 20px;font-weight: normal;color: #777;line-height: 50px;height: 50px;}
    .form .alert{height: 20px;line-height: 20px;font-size: 14px;color: #eb6100;}
    .form .el-input{margin:10px 0;}
    .form .el-button{margin-top:10px;}
    .form .button{width: 100%;background:#0099cc;color: #fff;}
    .form .rig{float:right;}
</style>